// variables

// colors
$blue: #5388F1;
$lightblue: #81B6F8;

$white: white;
$darkBlue: #0f1f37;
$black: #000000;
$dark-gray: #0E131B;
$grey: #636363;
$medium-gray: grey;
$light-gray: #EBF5FC;
$error-red: #FF4A4A;
$warning-color: yellow;
$success-color: green;
$primary-color: #5388F1;
$secondary-color: lightblue;
$main-fz: 16px;
$main-lh: 1.5;
$muli: 'Muli';
$unna: 'Unna';
$main-text-color: $black;
$headings-lh: 1.2;
$main-margin: $main-fz * $main-lh;

// grid settings
$gl-colCount: 24;
$gl-gridName: grid;
$gl-colName: col;
$gl-attributeName: class;
$gl-gutter: 30px; // Total left + right
$gl-gutter-vertical: 0;
$gl-mq-width: 'min-width';
$gl-mq-list: (
    xs: 320px,
    sm: 768px,
    md: 1200px,
    lg: 1800px
);

// media settings
$breakpoints: (
    mobile-landscape: 568px,
    tablet: 768px,
    tablet-landscape: 1024px,
    desktop: 1200px,
    medium-desktop: 1440px,
    large-desktop: 1800px,
    ultra-desktop: 2400px
);

$images-path: '../images/';

body {
    //font: #{$main-fz}/#{$main-lh};
    color: $main-text-color;
    background: white;
    -webkit-font-smoothing: antialiased;
}

h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
    // common styles
}

h1, .h1 {
    margin-bottom: #{24px * $headings-lh};
    //font-size: 24px;
    line-height: $headings-lh;
    color: $dark-gray;
    font-family: 'Unna', serif;
    font-size: 44px;
    font-weight: 700;
    line-height: 46px;
}

h2, .h2 {
    margin-bottom: #{22px * $headings-lh};
    font-size: 22px;
    line-height: $headings-lh;
    color: $dark-gray;
    font-family: 'Unna', serif;
    font-size: 44px;
    font-weight: 700;
    line-height: 46px;
}

h3, .h3 {
    margin-bottom: #{20px * $headings-lh};
    font-size: 20px;
    line-height: $headings-lh;
}

h4, .h4 {
    margin-bottom: #{18px * $headings-lh};
    font-size: 18px;
    line-height: $headings-lh;
}

h5, .h5 {
    margin-bottom: #{16px * $headings-lh};
    font-size: 16px;
    line-height: $headings-lh;
}

h6, .h6 {
    margin-bottom: #{14px * $headings-lh};
    font-size: 14px;
    line-height: $headings-lh;
}

p {
    margin-bottom: $main-margin;
}

a {
    text-decoration: none;
    color: $primary-color;
    outline: none;
    &:hover {
        color: darken($primary-color, 10%);
    }
}

ul {
    margin-bottom: $main-margin;
    li {
        padding-left: 20px;
        &:before {
        content: "";
        float: left;
        margin: 8px 0 0 -20px;
        border-radius: 50%;
        width: 6px;
        height: 6px;
        background: $primary-color;
        }
    }
}

ol {
    margin-bottom: $main-margin;
    counter-reset: counterName;
    li {
        &:before {
            counter-increment: counterName;
            content: counters(counterName, "-") ". ";
        }
    }
    ul,
    ol {
        margin: 0;
        padding-left: 20px;
    }
}

// forms

input[type="text"],
input[type="password"],
input[type="email"],
input[type="tel"],
input[type="number"] {
    display: inline-block;
    margin: 0 0 $main-margin;
    border: 1px solid $light-gray;
    padding: 5px 20px;
    line-height: 30px;
    font-size: 16px;
    height: 42px;
    vertical-align: middle;
    background: $white;
    outline: none;
    -webkit-appearance: none;
    -webkit-border-radius: 0;
    @include placeholder {
        color: $dark-gray;
    }
    &:focus {
        border-color: $primary-color;
    }
}

select {
    display: inline-block;
    margin: 0 0 $main-margin;
    border: 1px solid $light-gray;
    padding: 5px 20px;
    line-height: 30px;
    font-size: 16px;
    height: 42px;
    vertical-align: middle;
    background: $white;
    outline: none;
    &:focus {
        border-color: $primary-color;
    }
}

.button,
input[type="submit"],
button {
    width: 139px;
    height: 45px;
    background: $lightblue;
    border-left: 13px solid $blue;
    color: $white;
    font-family: 'Unna', serif;
    font-size: 16px;
    padding: 10px;
    font-weight: 400;
    position: relative;
    text-align: center;
    padding-right: 25px;
    transition: background 0.35s ease-in-out;
    cursor: pointer;
    -webkit-appearance: none;
    -webkit-border-radius: 0;
    @include media('>=desktop'){
        width: 180px;
        height: 55px;
        font-size: 20px;
        font-weight: 400;
        padding-left: 12px;
        padding-right: 40px!important;
        padding-top: 14px;
    }
 
    span{
        width: 45px;
        height: 3px;
        background-color: $blue;
        position: absolute;
        left: 85%;
        top: 50%;
        &:before{
            content: "";
            width: 10px;
            height: 3px;
            position: absolute;
            transform: translate(12px, 3px) rotate(-32deg);
            background-color: $blue;
        }
    }

    &:hover,
    &:focus {
        color: $white;
        background: darken($primary-color, 10%);
    }
    &.secondary {
        background: $secondary-color;
        &:hover,
        &:focus {
            background: darken($secondary-color, 10%);
        }
    }
    &.danger {
        background: $error-red;
        &:hover,
        &:focus {
            background: darken($error-red, 10%);
        }
    }
    &.warning {
        background: $error-red;
        &:hover,
        &:focus {
            background: darken($warning-color, 10%);
        }
    }
    &.success {
        background: $success-color;
        &:hover,
        &:focus {
            background: darken($success-color, 10%);
        }
    }
}

.button_footer{
    width: 180px;
    height: 55px;
    color: $white;
    font-family: $unna;
    font-size: 20px;
    font-weight: 400;
    background: $blue;
    padding-top: 15px;
    padding-left: 0px;
    border-left: 13px solid white;
    @include media ('>=desktop'){
        padding-left: 5px;
    }
    @include media('>=large-desktop'){
        width: 210px;
        height: 65px;
        font-size: 22px;
        font-weight: 400;
        padding-top: 20px;
    }
    span{
        background: white;
        @include media ('>=desktop'){
               margin-right: -70px;
        }
        @include media ('>=large-desktop'){
            margin-right: -75px;
            margin-top: 12px;
            
     }
        &:before{
            content: "";
            width: 10px;
            background-color: white;
            @include media ('>=large-desktop'){
                width: 15px;
                transform: translate(23px, 4px) rotate(-40deg);
            }
           
        }
        &:after{
            content: "";
            transform: translate(72px, 16px) rotate(-32deg);
            background: white;
            @include media ('>=desktop'){
                transform: translate(117px, -30px) rotate(-32deg);
            }
        }
    }
}

input[type="checkbox"] {
    display: inline-block;
    margin: 0 0 $main-margin;
    vertical-align: middle;
}

input[type="radio"] {
    display: inline-block;
    margin: 0 0 $main-margin;
    vertical-align: middle;
}

label {
    display: inline-block;
    margin: 0 0 $main-margin;
    vertical-align: middle;
}

blockquote {
    margin: 0 0 $main-margin;
    p {
        margin: 0;
    }
    q {
        quotes: none;
        &:before,
        &:after {
            quotes: none;
        }
    }
    cite {
        display: block;
    }
}

table {
    margin: 0 0 $main-margin;
    th,
    td {
        border: 1px solid $medium-gray;
        padding: 5px;
    }
}

[class^="col"] {
    > * {
        background: rgba(147, 128, 108, 0.2);
    }
}