.join_us{
    min-height: 614px;
    background-color: $light-gray;
 
    .join_us_main{
        padding-top: 70px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        @include media('>=desktop'){
            flex-direction: row;
            padding-top: 100px;
            min-height: 450px;
            flex-wrap: wrap;
            align-items: stretch;
            padding-bottom: 50px;
        }
        @include media('>=large-desktop'){
            padding-top: 125px;
            padding-bottom: 110px;
        }
        .advantage{
            margin-bottom: 15px;
            width: 200px;
            min-height: 390px;
            box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.08);
            background-color: $white;
            color: $dark-gray;
            font-family: $muli;
            font-size: 16px;
            font-weight: 400;
            line-height: 24px;
            padding: 18px 35px 0px 18px;
            @include media('>=desktop'){
                width: 270px;
                min-height: 425px;
                text-align: left;
                padding: 35px 35px 0px 25px;
                font-size: 17px;
                font-weight: 400;
                line-height: 26px;
                min-height: 425px;
                height: 100%;  
            }
            @include media('>=large-desktop'){
                width: 381px;
                min-height: 480px;
                padding: 35px 35px 0px 45px;
            }
            a{
                @include media('>=desktop'){
                    width: 175px;
                    display: block
                }
                @include media('>=large-desktop'){
                    width: 225px;
                }
                img{
                    @include media('>=large-desktop'){
                        width: 50px;
                    }
                }
                h2{
                    color: $dark-gray;
                    font-family: $unna;
                    font-size: 40px;
                    font-weight: 700;
                    line-height: 42px;
                    padding-top: 3px;
                    margin-bottom: 8px;
                    @include media('>=desktop'){
                        font-size: 46px;
                        font-weight: 700;
                        line-height: 48px;
                        padding-top: 20px;
                        margin-bottom: 11px;
                    }
                    @include media('>=large-desktop'){
                        font-size: 54px;
                        font-weight: 700;
                        line-height: 56px;
                    }
                    &:hover{
                        color: $lightblue;
                    }
                }
                span{
                    width: 148px;
                    height: 4px;
                    background-color: $blue;
                    position: relative;
                    display: block;
                    @include media('>=desktop'){
                        width: 100%;
                    }
                    &:before{
                    content: "";
                    width: 12px;
                    height: 4px;
                    background: $blue;
                    transform: translate(137px, 4px) rotate(132deg);
                    position: absolute;
                    @include media('>=desktop'){
                        transform: translate(161px, 4px) rotate(140deg);
                        width: 15px;
                    }
                    @include media('>=large-desktop'){
                        transform: translate(212px, 4px) rotate(140deg);
                        width: 15px;
                    }
                }
            }
           
            }
            p{
                margin-top: 30px;
                @include media ('>=desktop'){
                    line-height: 26px;
                    margin-top: 26px;
                    word-spacing: 2px;
                }
            }
        }
        .advantage:nth-child(n+3){
            display: none;
            @include media ('>=desktop'){
                display: block;
            }
        }
        .advantage:nth-child(odd){
            @include media ('>=desktop'){
                margin-top: 77px;
                
            }
        }
        .advantage:nth-child(even){
            @include media ('>=desktop'){
                margin-top: -15px;
            }
        }
        .advantage:nth-child(4){
            a {
                @include media ('>=desktop'){
                    padding-top: 13px;
                }
               
            }
            
        }
    }
}