.header {
    box-shadow: 0 2px 14px rgba(0, 0, 0, 0.13);
    background-color: $white;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 15px;
    z-index: 2;
    position: fixed;
    width: 100%;
    @include media('>=desktop'){
        box-shadow: 0 2px 14px rgba(0, 0, 0, 0.13);
        position: fixed;
        padding-left: 40px;
        padding-right: 40px;
    }
    @include media('>=large-desktop'){
        justify-content: center;
    }
    .main_logo{
        img{
            width: 102px;
            height: 43px;
            @include media('>=desktop'){
                width: 182px;
                height: 77px;
            }
        }
    }
    .header_nav{

        @include media('>=large-desktop'){
            flex-grow: 0.3;
        }
    .btn{
            display:block;
            position:relative;
            overflow:hidden;
            transition:all 0.5s ease;
            width: 40px;
            height: 40px;
            background-color: $blue;
            border-top: 4px solid $lightblue;
            span{
            width: 20px;
            height: 2px;
            position:absolute;
            top:60%;
            left:100%;
            text-indent:-9999px;
            margin:-4px 0 0 -30px;
            background:#fff;
            transition:all 0.5s ease-in-out;
	        }
        span:after, span:before{
            width:100%;
            height:2px;
            position:absolute;
            content:'';
            left:0;
            right:0;
            top:6px;
            background:#fff;
            transition:all 0.5s ease-in-out;
        }
        span:before{
            top:-6px;
        }
        &.active span{
            background:none;
            transform:rotate(136deg);
            &:after{
                top: 2px;
                transform:rotate(-92deg);
            }
            &:before{
                top:2px;
                transform:rotate(-182deg);
            }
        }
        
        @include media ('>=desktop'){
            display: none;
        }
	}

	
	.nav{
		margin:0;
		padding:0;
		background:$white;
		display:none;
		position:absolute;
		left: 0px;
		right: 0px;
		list-style:none;
        height: 100vh;
        align-items: center;
        justify-content: center;
        @include media('>=desktop'){
            height: 100%;
            display: block;
            position: relative;
        }
        @include media('>=large-desktop'){
            height: 100%;
            display: flex;
            position: relative;
            justify-content: flex-end;
        }
        ul{
        @include media('>=desktop'){
            display: flex;
            flex-direction: row;
            margin: 0px;
        }
        li{
            text-align:center;
            position: relative;
            padding-left: 0px;
            margin-bottom: 20px;
            @include media('>=desktop'){
                margin-bottom: 0px;
                margin-left: 70px;
                &:hover .dropdown{
                    display: block;
                }
            }
            @include media('>=large-desktop'){
                margin-bottom: 0px;
                margin-left: 70px;
                &:hover .dropdown{
                    display: block;
                }
            }
            &:before{
                display: none;
            }
            i{
                width: 6px;
                height: 6px;
                background-color: $blue;
                border-radius: 0px;
                display:block;
                margin: 0 auto;
                @include media('>=desktop'){
                    margin: 21px -38px;
                    position: absolute;
                
                }
            }
            a{
		        padding:13px;
		        display:block;
		        text-decoration:none;
                color: $black;
                font-family: $unna;
                font-size: 20px;
                font-weight: 400;
                @include media('>=desktop'){
                    //padding: 6px;
                }
                &:hover{
                    color: goldenrod;
                }
	        }
            .dropdown{
                    display: none;
                    z-index: 1;
                    position: relative;
                    width: 100%;
                    height: max-content;
                    padding-top: 0px;
                    margin-top: -10px;
                    @include media('>=desktop'){
                        position: absolute;
                        background: #fff;
                        width: 300px;
                        margin-left: -100px;
                        margin-top: 0px;
                        padding-top: 50px;
                        padding-bottom: 25px;
                    }
                    li{
                        margin-bottom: 0px;
                        a{
                            padding: 11px;
                        }
                        @include media('>=desktop'){
                            margin-left: 45px;
                        }
                        @include media('>=large-desktop'){
                            margin-left: 0px;
                        }
                    }
            }
            .toggler{
                position: absolute;
                width: 20px;
                height: 20px;
                top: 48%;
                left: 80%;
                transition: all 0.5s ease-in-out;
                @include media('>=desktop'){
                    display: none;
                }
                        &:before {
                            content: "";
                            transform:rotate(50deg);
                            position: absolute;
                            background-color: $black;
                            width: 2px;
                            height: 10px;
                            border-radius: 3px;
                        }
                        &:after{
                            content: "";
                            position: absolute;
                            background-color: $black;
                            transform:rotate(130deg);
                            width: 2px;
                            height: 10px;
                            top: -30%;
                            border-radius: 3px;
                        }
                    @include media('>=desktop') {
                        display: none;
                    }
                }
                .rotate{
                    transform: rotate(90deg);
                    width: 20px;
                    height: 20px;
                    position: absolute;
                    top: 6%;
                    left: 65%;
                    transition: all 0.5s ease-in-out;
                }
        }
        }
        
	}
	
 }
}