.footer {
    background-color: $darkBlue;
    padding-top: 30px;
    @include media ('>=desktop'){
        min-height: 331px;
        padding-top: 68px;
    }
    .footer_main{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: space-between;
        padding: 0 10px;
        @include media ('>=tablet'){
            align-items: center;
        }
        @include media ('>=desktop'){
            flex-direction: row;
            align-items: flex-start;
            flex-wrap: wrap;
            padding: 0px;
        }
            .social_buttons{
                text-align: center;
                margin-top: 5px;
                padding-right: 4px;
                min-width: 280px;
                display: flex;
                flex-direction: row;
                align-items: flex-start;
                justify-content: space-between;
                @include media ('>=tablet'){
                    width: 50%;
                }
                @include media ('>=desktop'){
                    width: 300px;
                    order: 2;
                    margin-top: 0px;
                    margin-left: -105px;
                    justify-content: space-evenly;
                }
                @include media ('>=large-desktop'){
                    width: 270px;
                    justify-content: space-around;
                    margin-left: 175px;
                }
                .btn{
                    min-width: 55px;
                    min-height: 55px;
                    background-color: $blue;
                    margin-bottom: 0px;
                    padding-top: 6px;
                    &:hover{
                        background: $lightblue;
                    }
                    img{
                        padding-top: 10px;
                    }
                    
                }
            }
            .contact_us{
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                //justify-content: space-between;
                //width: 100%;
                min-height: 130px;
                margin-top: 60px;
                @include media ('>=tablet'){
                    align-items: center;
                }
                @include media ('>=desktop'){
                    width: 250px;
                    order: 3;
                    margin-top: 0px;
                    align-items: flex-start;
                    padding-left: 5px;
                }
                @include media ('>=large-desktop'){
                    width: 270px;
                }
                .button_footer:nth-child(n+2){
                    margin-top: 20px;
                    @include media ('>=desktop'){
                        margin-top: 15px;  
                    }
                    @include media ('>=large-desktop'){
                        margin-top: 23px;  
                    }
                }
            }
            .address{
                width: 100%;
                min-height: 50px;
                margin-top: 61px;
                @include media ('>=tablet'){
                    width: 45%;
                }
                @include media ('>=desktop'){
                    width: 360px;
                    order: 1;
                    margin-top: 0px;

                }
                @include media ('>=large-desktop'){
                    width: 360px;
                    
                }
                img{
                    float: left;
                    line-height: 24px;
                    @include media ('>=desktop'){
                        margin-top: 7px;       
                    }
                    }
                    address{
                        opacity: 0.6;
                        color: white;
                        font-family: $muli;
                        font-size: 16px;
                        font-weight: 400;
                        word-spacing: 0.5px;
                        padding-left: 68px;
                        line-height: 23px;
                        font-style: normal;
                        @include media ('>=desktop'){
                            font-size: 22px;
                            font-weight: 400;
                            line-height: 30px;
                            word-spacing: 1.5px;
                            padding-left: 78px;
                        }
                }
            }
            .phone_email{
                min-height: 80px;
                padding-top: 16px;
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                justify-content: space-between;
                margin-top: 33px;
                @include media ('>=tablet'){
                    align-items: center;
                }
                @include media ('>=desktop'){
                    width: 720px;
                    order: 4;
                    min-height: 100px;
                    margin: 0;
                    align-items: flex-start;
                }
                a{
                    color: white;
                    font-family: 'Unna', serif;
                    font-size: 24px;
                    font-weight: 400;
                    @include media ('>=desktop'){
                        font-size: 32px;
                        font-weight: 400;
                    }
                    &:hover{
                        border-bottom: 1px solid $lightblue;
                    }
                }
            }
            
    }
    .terms_conditions{
        display: flex;
        margin-top: 50px;
        flex-direction: row;
        padding-right: 10px;
        flex-wrap: wrap;
        align-items: flex-start;
        justify-content: flex-start;
        @include media ('>=tablet'){
           min-width: 410px;
           justify-content: center;
        }
        @include media ('>=desktop'){
            width: 710px;
            order: 5;
            margin-top: 17px;
            justify-content: space-between;
            margin-bottom: 25px;
        }
        @include media ('>=desktop'){
            width:100%;

        }
        color: $light-gray;
        font-family: $muli;
        font-size: 14px;
        font-weight: 400;
        line-height: 16px;
        width: 100%;
        margin-top: 50px;
        p{
            opacity: 0.6;
            margin-bottom: 14px;
            line-height: 17px;
        }
        a{
            color: $light-gray;
            font-family: $muli;
            font-size: 14px;
            font-weight: 400;
            line-height: 16px;
            text-decoration: underline;
            margin-left: 20px;
            word-spacing: 1px;
            @include media ('>=desktop'){
               margin-left: 20px;
            }
            &:hover{
                text-decoration: none;
            }
        }
    }
}