.hero_section{
    background-image: url('/assets/img/banner_tablet_mobile.png');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: 50% 50%;
    width: 100%;
    min-height: 100vh;
    @include media('>=desktop'){
        background-image: url('/assets/img/hero_bg.png');
    }
    .hero_main{
        display: flex;
        flex-flow: column wrap;
        justify-content: center;
        align-items: center;
        height: 100vh;
            .hero_logo{
                img{
                    max-width: 100%;
                }
            }

        }
        .hero_btn{
            background-color: $blue;
            height: 40px;
            width: 35px;
            text-align: center;
            border-top: 8px solid $lightblue;
            padding: 5px;
            margin: 100px auto 0;
            transition: background 0.35s ease-in-out;
            @include media('>=desktop'){
                height: 54px;
                width: 50px;
                margin-top: 75px;
                border-top: 10px solid $lightblue;
                padding: 15px;
            }
            @include media('>=large-desktop'){
                margin-top: 180px;
            }
            a{
                @include media('>=desktop'){
                    width: 100%;
                    text-align: center;
                }
                img{
                    @include media('>=desktop'){
                        width: 20px;
                    }  
                }
            }
            &:hover{
                background-color: $lightblue;
            }
        }
}
    