.numbers{
    padding-top: 25px;
    .numbers_main{
        color: $dark-gray;
        font-family: $muli;
        font-size: 18px;
        font-weight: 400;
        line-height: 28px;
        @include media('>=desktop'){
            font-size: 22px;
            font-weight: 400;
            line-height: 34px;
            display: flex;
            flex-direction: column;
            align-items: flex-end;
  
        }
        @include media('>=large-desktop'){
                margin-top: 229px;
                font-size: 26px;
                font-weight: 400;
                line-height: 40px;
            }
        .text{
            @include media('>=desktop'){
                width: 640px;
            }
            @include media('>=large-desktop'){
                width: 750px;
            }
            p {
                
                span{
                    color: $blue;
                    strong{
                        font-weight: 600;
                        @include media('>=large-desktop'){
                            font-size: 32px;
                            font-weight: 600;
                        }
                    }
                } 
            }
        }
        
        .main_block {
            width: 62%;
            margin: 0px auto;
            @include media('>=desktop'){       
                position: relative;   
                width: 100%;
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: center;
                z-index: 1;
            }
            @include media('>=desktop'){
                margin-top: 5px;
                height: 375px;
            }
            &:before{
                @include media('>=desktop'){
                    content: "";
                    background-image: url(/assets/img/numbers_logo.png);
                    background-repeat: no-repeat;
                    background-size: cover;
                    width: 470px;
                    position: absolute;
                    height: 619px;
                    top: -245px;
                    left: 3px;
                }
                @include media('>=large-desktop'){
                    content: "";
                    background-image: url(/assets/img/clock_numbers_1920.png);
                    background-repeat: no-repeat;
                    background-size: cover;
                    width: 570px;
                    position: absolute;
                    height: 700px;
                    top: -260px;
                    left: 0px;
                }
                
            }
            &:after{
                @include media('>=desktop'){
                    content: "";
                    background-color: $light-gray;
                    height: 329px;
                    z-index: -1;
                    width: 103vw;
                    position: absolute;
                }
                
            }

            .numbers_info{
                @include media('>=tablet'){
                    display: flex;
                    flex-flow: row;
                    justify-content: center;
                }
                @include media('>=desktop'){
                    margin-left: 350px;
                }
                &:before{
                    display: none;
                }
                div:nth-child(1){
                    width: 181px;
                    background: $blue;
                    min-height: 161px;
                    position: relative;
                    padding-top: 70px;
                    padding-left: 15px;
                    padding-right: 15px;
                    color: $white;
                        font-size: 84px;
                        font-weight: 400;
                    @include media('>=desktop'){
                        height: 200px;
                        width: 225px;
                        padding-top: 80px;
                        padding-left: 10px;
                        padding-right: 25px;
                        font-size: 116px;
                            font-weight: 400;
                    }
                    p{
                        span{
                            width: 25px;
                            height: 3px;
                            transform: translate(1px, 22px) rotate(90deg);
                            background: white;
                            position: absolute;
                            @include media('>=desktop'){
                                transform: translate(-2px, 35px) rotate(90deg);
                            }
                            &:before{
                                content: "";
                                width: 25px;
                                height: 3px;
                                transform: translate(0px, 0px) rotate(90deg);
                                background: white;
                                position: absolute;
                            }
                        }
                        &:before{
                            content: "years";
                            position: absolute;
                            font-size: 20px;
                            font-weight: 400;
                            color: white;
                            left: 65%;
                            top: 32%
                        }
                    }
                }
                div:nth-child(2){
                    width: 181px;
                    padding: 17px;
                    text-align: center;
                    color: $white;
                    font-family: $unna;
                    font-size: 18px;
                    font-weight: 400;
                    line-height: 24px;
                    background: $lightblue;
                    @include media('>=desktop'){
                        width: 260px;
                        height: 200px;
                        text-align: left;
                        padding-top: 32px;
                        padding-right: 40px;
                        padding-left: 32px;
                    }
                    p{
                        @include media('>=desktop'){
                            font-size: 22px;
                            font-weight: 400;
                            line-height: 28px;
                            margin-left: 0px;
                        }
                    }
                }
                
            }
            .numbers_info:nth-child(2){
              margin-left: 20px;
              @include media('>=desktop'){
                display: none;
            
            }
              div:nth-child(2){
                padding: 0px 20px 20px 20px;
              }
            }
        }
    }
}