.explore_expertize{
    margin: 70px auto 0px;
    @include media ('>=desktop'){
        margin: 150px auto 50px;
    }
    .explore_expertize_main{
        text-align: center;
    .explore_block{
            width: 290px;
            min-height: 266px;
            padding-top: 31px;
            padding-left: 24px;
            padding-right: 10px;
            background-color: $blue;
            background-image: url('/assets/img/expertize_before.png');
            background-repeat: no-repeat;
            background-position-y: bottom;
            background-size: contain;
            color: $white;
            font-family: $muli;
            font-size: 16px;
            font-weight: 400;
            line-height: 24px;
            margin: 0px auto 15px;
            position: relative;
            text-align: left;

            @include media ('>=desktop'){
                display: inline-block;
                width: 570px;
                padding-top: 115px;
                padding-bottom: 90px;
                padding-left: 82px;
                padding-right: 65px;
                margin: 15px 20px 15px;
            }
            @include media ('>=large-desktop'){
                width: 575px;
                min-height: 330px;
                font-size: 17px;
                padding-left: 70px;
                padding-top: 55px;
                padding-bottom: 45px;
            }
            h2{
                color: $white;
                font-family: $unna;
                font-size: 28px;
                font-weight: 400;
                line-height: 46px;
                margin-bottom: 10px;
                @include media ('>=desktop'){
                    font-size: 44px;
                    font-weight: 400;
                    word-spacing: 4px;
                    line-height: 46px;
                    margin-bottom: 24px;
                }
                @include media ('>=large-desktop'){
                    font-size: 50px;
                    font-weight: 400;
                    line-height: 52px;
                    word-spacing: 0px;
                }
            }
            p{
                margin-bottom: 33px;
                @include media ('>=desktop'){
                    margin-top: -5px;
                    margin-bottom: 28px;
                }
                @include media ('>=large-desktop'){
                    margin-bottom: 38px;
                }
            }
            .button{
                span{
                    background: white;
                    &:before{
                        background: white;
                    }
                }
                padding-right: 37px;
                border-left: 13px solid white;
                position: absolute;
            }   
    } 
    .explore_block:nth-child(even){
        display: inline-block;
        background-color: $lightblue;
        @include media ('>=large-desktop'){
           padding-right: 35px;
           padding-left: 65px;
        }

        .button{
            background-color: $blue;
            &:hover{
                background: $lightblue;
            }
        }
    }
    }
}
