.we_are{
    @include media('>=desktop'){
        min-height: 45vh;
    }
}
.we_are_main{
        font-size: 18px;
        font-weight: 400;
        line-height: 28px;
        font-family: $muli;
        word-spacing: 3px;
        @include media('>=desktop'){
            
            font-size: 24px;
            font-weight: 400;
            line-height: 36px;
            float: left;
            width: 45%;
            padding-right: 20px;
        }
        @include media('>=large-desktop'){
            font-size: 28px;
            font-weight: 400;
            line-height: 44px;
        }
        .title{
            @include media('>=desktop'){
                margin-top: 75px;
            }
                &:before{
                    content: "";
                    background-color: $light-gray;
                    position: absolute;
                    min-width: 100vw;
                    left: 0%;
                    height: 106px;
                    z-index: -1;
                    @include media('>=desktop'){
                        height: 189px;
                    }
                }
                &:after{
                    display: none;
                }
                h1{
                    z-index: 1;
                    word-spacing: 0px;
                    padding-top: 30px;
                    @include media('>=desktop'){
                        font-size: 64px;
                        font-weight: 700;
                        line-height: 70px;
                        padding-top: 60px;
                    }
                }
        }
        p{
            @include media('>=tablet'){
                font-size: 20px;
            }
        }
            
}

.picture{
        margin: 70px auto 0px;
        @include media('>=desktop'){
            width: 50%;
            height: 596px;
            float: left;
            margin: 20px auto 0;
        }
        @include media('>=large-desktop'){

        }
        img{
            width: 100%;
        }
}