.employers{
    margin-top: 150px;
    @include media('>=desktop'){
        margin-top: 225px;
    }
    @include media('>=large-desktop'){
        margin-top: 320px;
    }
    .employers_main{

        .employers_photo{
            text-align: center;
            @include media('>=desktop'){
                width: 50%;
                height: 593px;
                float: right;
            }
            @include media('>=large-desktop'){
                width: 750px;
height: 785px;
margin-top: 0px;
            }
            i{
                &:before{
                    content: "";
                    position: absolute;
                    width: 270px;
                    height: 270px;
                    background-color: $light-gray;
                    margin-top: -45px;
                    margin-left: 40px;
                    z-index: -1;
                    @include media('>=desktop'){
                        width: 570px;
                        height: 593px;
                        margin-top: -50px;
                        margin-left: 70px;
                    }
                    @include media('>=large-desktop'){
                        width: 770px;
                        height: 785px;
                        margin-top: -90px;
    margin-left: 170px;
                    }
                }
                img{
                    max-width: 100%;
                }
            }
        }
        .we_help{
            padding-top: 30px;
            color: $dark-gray;
            font-family: $muli;
            font-size: 16px;
            font-weight: 400;
            line-height: 24px;
            padding-right: 10px;
            @include media('>=desktop'){
                width: 50%;
                padding-right: 25px;
                line-height: 26px;
                display: block;
            }
            @include media('>=large-desktop'){
                font-size: 22px;
font-weight: 400;
line-height: 31px;
width: 700px;
height: 700px;
margin-top: 90px;
padding-right: 35px;
            }
            &:before{
                @include media('>=desktop'){
                    content: "";
                    position: absolute;
                    left: -13%;
                    top: 44%;
                    width: 405px;
                    height: 324px;
                    background-image: url('/assets/img/logo_bg.png');
                }
            }
            h3{
                color: $dark-gray;
                font-family: $unna;
                font-size: 44px;
                font-weight: 700;
                line-height: 46px;
                margin-bottom: 16px;
                word-spacing: 3px;
                @include media('>=desktop'){
                    font-size: 64px;
font-weight: 700;
line-height: 70px;
word-spacing: 5px;
margin-bottom: 31px;
                }
                @include media('>=large-desktop'){
                    font-size: 64px;
font-weight: 700;
line-height: 70px;
padding-right: 75px;   
margin-bottom: 35px;    
                }
            }

            .we_help_buttons{

                .button:nth-child(2){     
                   margin-left: 50px;
                    
                }
            }

        }
    }
}